.nav {
  position: fixed;
  top: 50px;
  left: 20px;
  display: flex;
  flex-direction: column;
  background-color: white;
  padding: 10px 20px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  border-radius: 5px;
  z-index: 1;
}

.nav a {
  text-decoration: none;
  color: hsla(222, 89%, 30%, 100%);
  text-transform: uppercase;
  font-size: 12px;
  text-align: center;
  font-weight: 600;
}

.nav a:not(:last-child) {
  margin-bottom: 20px;
}


@media (max-width: 768px) {
  .nav {
    width: 100%;
    top: 0;
    left: 0;
    flex-direction: row;
    border-radius: 0;
  }

  .nav a:not(:last-child) {
    margin-bottom: 0;
    margin-right: 20px;
  } 
}